import { Button } from "react-bootstrap";
import { useState } from "react";
function Join() {
  let [inputValue, setInputValue] = useState("");

  return (
    <div className="join_">
      <h3> </h3>
      <div className="join">
        <colgroup>
          <col style={{ width: "150px" }}></col>
          <col style={{ width: "auto" }}></col>
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">*</th>
            <td>
              <input
                type="text"
                name="value"
                maxLength="16"
                placeholder=" ."
                onChange={(e) => {
                  console.log(e.target.value);
                  setInputValue(e.target.value);
                }}
              />
              <br />
              <span>(/, 4~16)</span>
            </td>
          </tr>

          <tr>
            <th scope="row">*</th>

            <td>
              {" "}
              <input
                type="text"
                name="value"
                placeholder=" ."
                maxLength="16"
                minLength="10"
                secureTextEntry={true}
              ></input>
              <br />
              <span>
                ( //  2  , 10~16)
              </span>
            </td>
          </tr>
          <tr>
            <th scope="row">*</th>

            <td>
              {" "}
              <input type="text" placeholder=" ."></input>
              <br />
            </td>
          </tr>
          <tr>
            <th scope="row">*</th>

            <td>
              {" "}
              <input type="text" placeholder=" ."></input>
              <br />
            </td>
          </tr>
          <tr>
            <th scope="row"></th>

            <td>
              {" "}
              <input type="tel" style={{ width: "40px" }}></input>
              <span style={{ padding: "5px" }}>-</span>
              <input type="tel" style={{ width: "40px" }}></input>
              <span style={{ padding: "5px" }}>-</span>
              <input type="tel" style={{ width: "40px" }}></input>
              <br />
            </td>
          </tr>
          <tr>
            <th scope="row">*</th>

            <td>
              {" "}
              <input type="text" placeholder=" ."></input>
              <br />
            </td>
          </tr>
        </tbody>

        {/* <h4> </h4> */}
      </div>
      <Button variant="dark" style={{ margin: "50px" }}>
        
      </Button>
    </div>
  );
}

export default Join;
