import { useState, useEffect } from "react";
import "./scroll.css";

function Scroll() {
  const [ScrollY, setScrollY] = useState(0);
  const [BtnStatus, setBtnStatus] = useState(false); //  

  const handleFollow = () => {
    setScrollY(window.pageYOffset);
    if (ScrollY > 100) {
      // 100   
      setBtnStatus(true);
    } else {
      // 100   
      setBtnStatus(false);
    }
  };

  const handleTop = () => {
    //     
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setScrollY(0); // ScrollY   
    setBtnStatus(false); // BtnStatus  false  =>  
  };

  useEffect(() => {
    const watch = () => {
      window.addEventListener("scroll", handleFollow);
    };
    watch();
    return () => {
      window.removeEventListener("scroll", handleFollow);
    };
  });

  return (
    <div className="wrap">
      <button
        className={BtnStatus ? "topBtn active" : "topBtn"} //   
        onClick={handleTop} //    
      >
        TOP
      </button>
      <div class="inner">
        {/* <h1>Fixed Scroll Top</h1>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Mollitia,
        voluptas ratione libero earum alias, officia temporibus magni possimus
        atque rem distinctio autem unde eaque. Minus quaerat odio mollitia
        asperiores neque? ... */}
      </div>
    </div>
  );
}

export default Scroll;
