import { Nav, Button, Tab, Tabs, Sonnet } from "react-bootstrap";
import { useState } from "react";

function ItemDetail() {
  let [tabIndex, setTabIndex] = useState(0);
  return (
    <div>
      <div className="detailArea">
        <div className="BigImg">
          <img style={{ float: "left" }} src="./img/pd1.jpg"></img>
        </div>
        <div className="infoArea">
          <div className="productTitle">
            <span> 220g</span>
          </div>
          <div className="summary">
            <ul>
              <li>
                <span>
                       
                  <br />
                     .
                  <br />
                          
                  .
                  <br />
                  1      .
                </span>
              </li>
              <li className="productPrice">
                <span className="title">
                  <span style={{ marginRight: "10px", marginTop: "10px" }}>
                    
                  </span>
                  <span style={{ marginTop: "10px" }}>15,000</span>
                </span>
              </li>
              <li className="productPrice">
                <span style={{ marginTop: "13px", marginRight: "10px" }}>
                  
                </span>
                <span style={{ fontSize: "30px", marginLeft: "10px" }}>
                  12,500
                </span>
              </li>
              <li>
                <span style={{ marginTop: "30px" }}></span>
                <sapn>
                  <input
                    className="quantity"
                    type="text"
                    value="1"
                    style={{ marginLeft: "100px", marginTop: "30px" }}
                  ></input>
                </sapn>
              </li>
              <li>
                <span style={{ marginTop: "30px" }}></span>
              </li>
            </ul>
          </div>
          <div className="totalProducts">
            <tr>
              {/* <td> 220g</td>
              <td>
                <span>
                  <input type="text" style={{ width: "30px" }}></input>
                </span>
              </td> */}
              <td>
                <span>
                  <input type="hidden"></input>
                </span>
              </td>
            </tr>
          </div>
          <div className="button">
            <div className="buyButton">
              <Button variant="dark" style={{ width: "100%", height: "60px" }}>
                <span
                  style={{
                    fontSize: "20px",
                    fontWeight: "bold",
                    letterSpacing: "3px",
                  }}
                >
                  
                </span>
              </Button>
              <Button
                variant="light"
                style={{
                  border: "1px solid lightgray",
                  width: "50%",
                  marginTop: "10px",
                }}
              >
                
              </Button>
              <Button
                variant="light"
                style={{
                  border: "1px solid lightgray",
                  width: "50%",
                  marginTop: "10px",
                }}
              >
                
              </Button>
            </div>
          </div>
        </div>
      </div>
      <div className="detail">
        {/* <p>pd001 </p> */}

        {/* <h3 className="menu_price">11000</h3> */}

        {/* <img className="detail_image" src="/img/cereal.jpg"></img> */}
        {/* <div className="detail_info">    </div> */}

        <div className="tab">
          <Nav variant="tabs" defaultActiveKey="link1">
            <Nav.Item>
              <Nav.Link
                style={{ color: "gray" }}
                eventKey="link1"
                onClick={() => {
                  setTabIndex(0);
                }}
              >
                <span style={{ fontSize: "15px", marginBottom: "5px" }}>
                  
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                style={{ color: "gray" }}
                eventKey="link2"
                onClick={() => {
                  setTabIndex(1);
                }}
              >
                <span style={{ fontSize: "15px", marginBottom: "5px" }}>
                  
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                style={{ color: "gray" }}
                eventKey="link3"
                onClick={() => {
                  setTabIndex(2);
                }}
              >
                <span style={{ fontSize: "15px", marginBottom: "5px" }}>
                  
                </span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                style={{ color: "gray" }}
                eventKey="link4"
                onClick={() => {
                  setTabIndex(3);
                }}
              >
                <span style={{ fontSize: "15px", marginBottom: "5px" }}>
                  
                </span>
              </Nav.Link>
            </Nav.Item>
          </Nav>
          {/* <Comp /> */}

          <Comp tabIndex={tabIndex} />
        </div>
      </div>
      {/* <div className="tabs">
        <Tabs
          defaultActiveKey="profile"
          id="fill-tab-example"
          className="mb-3"
          fill
        >
          <Tab eventKey="home" title=""></Tab>
          <Tab eventKey="profile" title=""></Tab>
          <Tab eventKey="longer-tab" title=""></Tab>
          <Tab eventKey="contact" title=""></Tab>
        </Tabs>
      </div> */}
    </div>
  );
}

function Comp({ tabIndex }) {
  let [inputValue, setInputValue] = useState("");
  return (
    <div>
      {
        [
          <div className="detail_photo">
            <img
              src="./img/detailInfo.jpg"
              style={{
                alignItems: "center",
                justifyContent: "center",
                marginTop: "150px",
              }}
            ></img>

            <img src="./img/detailInfo2.jpg"></img>
          </div>,
          <div className="reviewBoard" style={{ padding: "100px" }}>
            <div></div>
            <div className="productReview">
              <div></div>
            </div>
            {/* <input
              className="qna"
              type="text"
              placeholder=" ."
            ></input> */}
            {/* <button style={{ margin: "20px" }}></button> */}
          </div>,
          <div style={{ padding: "100px" }}>
            <form>
              {/* <input
                className="qna"
                type="text"
                style={{ marginTop: "100px" }}
                placeholder=" ."
                onChange={(e) => {
                  console.log(e.target.value);
                  setInputValue(e.target.value);
                }}
              ></input>
              <button
                style={{ margin: "20px" }}
                onClick={(e) => {
                  e.preventDefault();
                  console.log(inputValue);
                }}
              >
                
              </button>
              <p>{inputValue}</p> */}
            </form>
          </div>,
          <div style={{ padding: "100px" }}>
            {/* <img src="./img/info.jpg" style={{ marginTop: "100px" }}></img> */}
          </div>,
        ][tabIndex]
      }
    </div>
  );
}
export default ItemDetail;
